
#map{
	width: 100%;
	height: 35vh;
	box-sizing: border-box;
}
#detail-info{
	background-image: url('../assets/preview.jpg');
	width: 100%;
	padding: 20px;
	box-sizing: border-box;
	background-size: cover;
	background-position: center center;
}
.detail-info-container{
	width: 100%;
	height: 100%;
	background-color: white;
	overflow-y: scroll;
	box-sizing: border-box;
	padding: 40px;
	box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.detail-info-container h1 { 
	font-size: 45px;
	font-weight: bold;
	display: inline-block;
	margin: 0px;
}
.detail-info-container p {
	font-weight: lighter;
	display: inline;
	font-size: 0.8vh;
	line-height: 1.35;
	padding-left: 10px;
}
.detail-apply {
	text-align: center;
	margin-bottom: 60px;
}
.detail-apply button{
	padding: 30px 60px;
	font-size: 45px;
	background-color: #1976D2;
	color: white;
	border-radius: 100px;
	font-weight: bold;
	box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
.left-border{
	padding-left: 20px;
	border-left: 10px solid #1976D2;
}
.detail-div{
	margin-bottom: 60px;
	word-wrap: break-word;
	word-break: break-all;
}
.geo-link{
	position: absolute;
	top : 60px;
	right: 0px;
	padding: 30px;
	font-size: 40px;
	color: black;
	background-color: white;
	opacity: 0.6;
	z-index: 2;
	text-decoration: none;
	display: block;
}





